import axios from 'axios'
import qs from 'qs'

const http = axios.create({
    baseURL:'http://124.222.130.4:9001',
    // baseURL:'http://127.0.0.1:8000',
    timeout:3000,
    // 请求头带上token
    headers:qs.stringify({"aams-token":sessionStorage.getItem("aams-token") || ""}),
    withCredentials:true,
})


// request 请求拦截器
http.interceptors.request.use(
    config => {
      // 发起请求时，重新获取最新的token，这一步有时很重要，因为创建 axios 实例的时候，
      // 获取到的 token 未必是有效的，或说未必能获取到
      const token = sessionStorage.getItem('aams-token')
  
      // 特殊配置：登录接口，将 请求的头的 token 设置为空字符串
      if (config.url === '/login') {
        config.headers['aams-token'] = ''
      } else {
        config.headers['aams-token'] = token || ''
      }
  
      return config
    },
    // error => {
    //   console.warn(error)
    //   return Promise.reject(error)
    // }
  )

export default http