<template>
  <div>
    <!-- 我的信息部分 -->
    <el-card>
        <el-row>
        <el-col :span="2"><div style="font-size:25px;">{{userInfo.fullname}}</div></el-col>
        <el-col :span="16">
          <div style="font-size:18px; display:flex; justify-content:space-around;">
            <span>性别：{{userInfo.gender==0?"男":"女"}}</span> 
            <span>学号：{{userInfo.username}}</span>
            <span>学院：{{college.title}}</span>
            <span>专业：{{userInfo.major.title}}</span>
          </div>
        </el-col>
        <el-col :span="3" :offset="3">
          <el-button @click="closeSelect">退出选课</el-button>
        </el-col>
        </el-row>
    </el-card>
    <br>
    <!-- 选课部分 -->
    <el-row>
      <!-- 左边选课 -->
      <el-col :span="10">
        <el-card>
          <el-row>
            <el-col :span="12"><div style="font-size:18px; font-weight:bold; margin-bottom:20px;">选课列表</div></el-col>
            <el-col :span="12">
              <div style="display:flex; justify-content:flex-end;">
                <el-button @click="addCourseCart">添加课程</el-button>
              <!-- 保存课表 -->
                <el-button class="blue_btn" @click="saveCourse">双击刷新课表</el-button>
              </div>
            </el-col>
          </el-row>
          <el-table
            :data="showCourse"
            height="400"
            border
            style="width: 100%">
            <el-table-column label="课程名称" align="center">
              <template slot-scope="scope">
                {{scope.row.title}} <br>
                ({{scope.row.shortcode}})
              </template>
            </el-table-column>
            <el-table-column prop="credits" label="学分" align="center" width="60"></el-table-column>
            <el-table-column label="必/选" align="center" width="60">
              <template slot-scope="scope">
                <div v-if="scope.row.coursetype==1">必</div>
                <div v-else>选</div>
              </template>
            </el-table-column>
            <el-table-column prop="longcode" label="已选状态" align="center" width="120"></el-table-column>
            <el-table-column label="操作" align="center" width="60">
              <template slot-scope="scope">
                <el-button type="text" @click="lookOneCourse(scope.row.shortcode)">查看</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </el-col>
      <!-- 右边查看 -->
      <el-col :span="14">
        <el-card style="margin-left:20px;">
          <div v-if="lookCourse.length>0" style="font-size:18px; font-weight:bold; margin-bottom:20px;">
            {{lookCourse[0].title}} {{lookCourse[0].coursecode.substr(0,lookCourse[0].coursecode.length-2)}}
          </div>
          <div v-else style="font-size:18px; font-weight:bold; margin-bottom:20px;">待选课程</div>
          <el-table
            :data="lookCourse"
            height="400"
            border
            style="width: 100%">
            <el-table-column label="课程序号" align="center" width="100">
              <template slot-scope="scope">
                <div v-if="scope.row.coursecode==lookCourseGreen" style="display:flex; justify-content:center;"><el-button @click="alreadyOwned" type="success" size="medium" style="font-weight:bold;font-size:15px;">{{scope.row.coursecode}}</el-button></div>
                <div v-else style="display:flex; justify-content:center;"><el-button @click="trySelect(scope.row)" type="success" plain size="medium" style="font-weight:bold;font-size:15px;">{{scope.row.coursecode}}</el-button></div>
              </template>
            </el-table-column>
            <el-table-column prop="teacher.fullname" label="教师" align="center" width="100"></el-table-column>
            <el-table-column label="校区" align="center" width="100">
              <template slot-scope="scope">
                <div v-if="scope.row.campus==1" style="background:#F7DB78;">四平路校区</div>
                <div v-else-if="scope.row.campus==2" style="background:#F7E2DB;">嘉定校区</div>
                <div v-else-if="scope.row.campus==3" style="background:#ECEDEB;">线上课堂</div>
                <div v-else style="background:#DCDFE6;">其他校区</div>
              </template>
            </el-table-column>
            <el-table-column label="已选/上限" align="center" width="100">
              <template slot-scope="scope">
                <div>
                  {{scope.row.selectednumber}} / {{scope.row.capacity}}
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="info" label="课程安排" align="center"></el-table-column>
            <el-table-column prop="note" label="备注" align="center" width="100"></el-table-column>
          </el-table>
        </el-card>
      </el-col>
    </el-row>
    <!-- 课表状态部分 -->
    <br>
    <el-card :key="scheduleKey">
      <ScheduleTable :schedule="schedule" />
    </el-card>
  </div>
</template>

<script>
import ScheduleTable from './ScheduleTable';
export default {
    name:"SelectCourseInfo",
    components:{
      ScheduleTable,
    },
    data() {
      return {
        // 基本信息部分
        userInfo:{},
        college:{},
        // 已选课程
        ownedCourse:[],
        // 待选课程
        showCourse:[],
        // 查看某一个选课
        lookCourse:[],
        lookCourseGreen:"",
        // 课表部分
        scheduleCourse:[],
        schedule:{},
        scheduleKey:0,
      }
    },
    methods: {
      // 刷新已选课程
      reloadOwnedCourse(){
        this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
      .then(res=>{
          // 获取基本信息、已选课程
          this.userInfo = res.data["data"]["user"];
          this.ownedCourse = res.data["data"]["user"]["courses"];
        })
      },

      // 保存课表
      saveCourse(){
        // 课表重新加载
        this.scheduleKey++;
        this.loadSchedule();

        const h = this.$createElement;
        this.$notify({
          title: '刷新成功',
          message: h('i', { style: 'color: teal'}, '刷新课表成功！'),
          position: 'top-left',
          offset: 150,
        });
      },
      // 添加待选课程
      addCourseCart(){
        this.$api.course.getMajorCourses(this.userInfo.major.majorcode,this.userInfo.username)
        .then(res=>{
          this.showCourse = res.data["data"]["courses"];
          const h = this.$createElement;
          this.$notify({
            title: '添加成功',
            message: h('i', { style: 'color: teal'}, '添加待选课程成功！'),
            position: 'top-left',
            offset: 150
          });
        })
      },

      // 查看某一个选课
      lookOneCourse(shortcode){
        this.$api.course.getShortCodeCourse(shortcode)
        .then(res=>{
          this.lookCourse = res.data["data"]["courses"];
          this.showCourse.forEach(item =>{
            if(shortcode==item["shortcode"]){
              this.lookCourseGreen = item["longcode"];
            }
          })
        })
      },
      // 点击已经选了的课程
      alreadyOwned(){
        // this.$notify({
        //   title: '已选提示',
        //   message: '您已选该课程，请勿重复操作！',
        //   type: 'info',
        //   offset:150
        // });
        this.$confirm('此操作将退课, 是否继续?', '退课提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.$api.course.withdrawalFromClass(sessionStorage.getItem("username"),this.lookCourseGreen);
          // 课表重新加载
          this.scheduleKey++;
          this.loadSchedule();
          this.reloadOwnedCourse();
          this.$notify({
            title: '退课成功',
            message: '您已成功退课！',
            type: 'success',
            offset:150
          });
          this.showCourse.forEach(item =>{
            if(item.shortcode==this.lookCourseGreen.slice(0,this.lookCourseGreen.length-2)){
              item.longcode = "";
            }
          })
          this.lookCourseGreen = "";
        }).catch(() => {
          this.$notify({
            title: '取消退课',
            message: '您已取消退课！',
            type: 'info',
            offset:150
          });
        });
      },
      // 点击未选的课程
      trySelect(tryCourse){
        if(this.checkconflict(tryCourse)){
          if(this.lookCourseGreen!=""){
            this.$api.course.withdrawalFromClass(sessionStorage.getItem("username"),this.lookCourseGreen);
          }
          this.$api.course.selectCourse(sessionStorage.getItem("username"),tryCourse.coursecode)
          .then(()=>{
            this.lookCourseGreen = tryCourse.coursecode;
            this.showCourse.forEach(item =>{
              if(item.shortcode==this.lookCourseGreen.slice(0,this.lookCourseGreen.length-2)){
                item.longcode = tryCourse.coursecode;
              }
            })
            // 课表重新加载
            this.scheduleKey++;
            this.loadSchedule();
            this.reloadOwnedCourse();
            this.$notify({
              title: '选课成功',
              message: '您已成功保存选课！',
              type: 'success',
              offset:150
            });
          })
          .catch(err=>{
            this.$notify({
              title: '选课失败',
              message: err.response.data["msg"],
              type: 'error',
              offset:150
            });
          })
        }
        else{
          console.log("不可选");
          this.$notify({
            title: '冲突提示',
            message: '该课程与已选课程有冲突！',
            type: 'error',
            offset:150
          });
        }
      },
      // 添加选课前的冲突判断
      checkconflict(tryCourse){
        // 先刷新已有课程
        this.reloadOwnedCourse();
        // console.log(this.ownedCourse);
        let owned_odd_all = []
        let owned_even_all = []
        this.ownedCourse.forEach(course=>{
          let owneddict = this.$api.course.readCourse(course);
          owned_odd_all = [...owned_odd_all,...owneddict.classtime_odd_all];
          owned_even_all = [...owned_even_all,...owneddict.classtime_even_all];
        })
        // console.log(owned_odd_all,owned_even_all);
        // console.log(owned_even_all.includes("103"));
        // console.log(tryCourse);
        let trydict = this.$api.course.readCourse(tryCourse);
        let flag = true;
        trydict.classtime_odd_all.forEach(t=>{
          if(owned_odd_all.includes(t)){
            flag = false;
          }
          // console.log(owned_odd_all.includes(t));
        });
        trydict.classtime_even_all.forEach(t=>{
          if(owned_even_all.includes(t)){
            flag = false;
          }
        });
        // console.log(flag);
        return flag;
      },


      // 退出选课
      closeSelect(){
        this.$router.push("/student/selectCourse");
        this.$message({
          message:"已退出选课！",
          type:"success"
        })
      },

      //以下是课程表使用的：
      // 刷新课表
      reloadSchedule(){
        this.loadSchedule();
        this.scheduleKey++;
      },
      // 加载课表
      loadSchedule(){
        this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
        .then(res=>{
          this.scheduleCourse = res.data['data']['user']['courses'];
          this.schedule = this.$api.course.readSchedule(this.scheduleCourse);
        })
        .catch(err=>{
          this.$message({
            message:"获取课表出错！"+err.response.data["msg"],
            type:"error"
          })
        });
      },
    },
    created(){
      this.loadSchedule();
    },
    mounted() {
      this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
      .then(res=>{
        // 获取基本信息、已选课程
        this.userInfo = res.data["data"]["user"];
        this.ownedCourse = res.data["data"]["user"]["courses"];
        // 获取学院信息
        this.$api.userInfo.getCollege(this.userInfo.major.pcode)
        .then(res=>{
          this.college = res.data["data"]["college"];
        })
        .catch(()=>{
          this.college = this.userInfo.major;
        });
      })
    }
}
</script>

<style>

</style>