<template>
  <div>
    <div class="nav_title">个人选课</div>
      <div v-if="stage!=2" class="noavailable">
        当前没有对您开放的选课！<el-button @click="reload">刷新</el-button>
      </div>
      <div v-else>
        <el-card>
          <div>
            <div style="font-weight:bold;font-size:20px;">{{year}}-{{year+1}}学年第{{currentTerm}}学期 第一轮</div>
            <el-row>
              <el-col :span="20">
                <p>选课开放时间：{{today}} </p>
                <p>退课开放时间：{{today}} </p>
                <p>选课规则：限制选修课门数 | 按教学班选课 | 不允许时间冲突 |如果作弊，必须隔一个学期才能重修 | 限制新选学分与重修门数 | 只能选一门体育课 | 检查英语课等级 | 通过课程不允许重修 | 第一轮选满第二轮不能选 | 缴费登陆限制 | 在可选名单内才能选课 | 2018级以后学分门数限制 | 先修课限制</p>
                <p>选课说明：1.每学期第一次进入选课界面前请先输入“选课须知”的内容，以后每次进入直接点确定即可，需使用中文标点符号、不要有空格。2.选课完毕，请务必进入“我的课表”确认最终选课结果（包括选课申请处理结果）。正式上课前也请再次查看“我的课表”确认上课时间地点等信息。</p>
              </el-col>
              <el-col :span="4">
                <el-button class="blue_btn" style="margin-top:30px;width:120px;height:45px;" @click="dialogVisible = true">进入选课</el-button>
                <el-dialog
                  title="选课须知"
                  :visible.sync="dialogVisible"
                  width="60%">
                  <h3 style="display:flex;justify-content:center;">请阅读下面的内容</h3>
                  <div style="background-color:#DCDFE6;padding:10px;">我已知晓教育部令第41号关于真实、完整地记载、出具学生学业成绩的相关规定，以及学生手册上与选课、成绩相关的所有规定，仔细阅读了学校本学期选课通知，将按要求完成选课并及时核对结果，认真修读包括本人自选及教务员、管理员代理选的所有课程，经常查看我的课表及各类相关通知，主动避免各种失误，并愿意承担相应后果。</div>
                  <span slot="footer" class="dialog-footer" style="display:flex;justify-content:center;">
                    <el-button class="blue_btn" @click="openSelect">确 定</el-button>
                  </span>
                </el-dialog>
              </el-col>
            </el-row>
          </div>
        </el-card>
      </div>
  </div>
</template>

<script>
export default {
    name:"SelectCourse",
    data() {
      return {
        currentTerm:0,
        stage:1,
        year:0,
        today:"",
        dialogVisible:false,

      }
    },
    methods: {
      // 打开进入选课
      openSelect(){
        this.dialogVisible = false;
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(()=>{
          loading.close();
          this.$message({
            message:"已进入选课！",
            type:"success"
          })
        },1000);
        // 跳转选课页面
        this.$router.push('/student/selectCourseInfo');
      },
      // 刷新
      reload(){
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        // setTimeout(()=>{
        //   loading.close();
        //   this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
        //   .then(res=>{
        //     this.currentTerm = res.data["data"]["user"]["term"];
        //     this.stage = res.data["data"]["user"]["stage"];
        //     this.$message({
        //       message:"刷新成功",
        //       type:"success",
        //     });
        //   })
        //   .catch(err=>{
        //     loading.close();
        //     this.$message({
        //       message:err.response.data["msg"],
        //       type:"error",
        //     });
        //   })
        // },1000);
        this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
        .then(res=>{
          this.currentTerm = res.data["data"]["user"]["term"];
          this.stage = res.data["data"]["user"]["stage"];
          loading.close();
          this.$message({
            message:"刷新成功",
            type:"success",
          });
        })
        .catch(err=>{
          loading.close();
          this.$message({
            message:err.response.data["msg"],
            type:"error",
          });
        })
      }
    },
    mounted(){
      this.year = new Date().getFullYear();
      this.today = new Date().toISOString();
    }
}
</script>

<style scoped>
  .noavailable{
    display: flex;
    justify-content: center;
    color: #909399;
    font-size: 25px;
  }
</style>