import http from "../utils/http"
import qs from 'qs'

export function login(data={}){
    return http({
        method:"POST",
        url:"login/",
        data:qs.stringify(data)
    })
}

export function getUserInfo(username){
    return http({
        method:"GET",
        url:`getUserInfo/${username}`,
    })
}

export function changePassword(username,data={}){
    return http({
        method:"POST",
        url:`getUserInfo/${username}`,
        data:qs.stringify(data),
    })
}

export function getCollege(code){
    return http({
        method:"GET",
        url:`getCollege/${code}`,
    })
}