<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <div class="nav_title">我的信息</div>
      </div>
      <el-collapse v-model="activeNames" class="myinfo_all">
        <el-collapse-item name="1" class="info">
          <template slot="title">
            <span class="spantitle">基本信息</span>
          </template>
          <div>姓名：<el-tag type="success" size="small">{{userInfo.fullname}}</el-tag></div>
          <div>账号：<el-tag type="success" size="small">{{userInfo.username}}</el-tag></div>
          <div>身份：{{userInfo.identity}}</div>
          <div>专业：{{major.title}}</div>
        </el-collapse-item>
        <el-collapse-item name="2" class="info">
          <template slot="title">
            <span class="spantitle">更多信息</span>
          </template>
          <div>所属学院：<el-tag size="small">{{college.title}}</el-tag></div>
          <div>学院代码：{{college.majorcode}}</div>
          <div>修读专业：<el-tag size="small">{{major.title}}</el-tag></div>
          <div>专业代码：{{major.majorcode}}</div>
          <div>当前学期：{{userInfo.term}} - {{userInfo.stage}}</div>
        </el-collapse-item>
        <el-collapse-item name="3">
          <template slot="title">
            <span class="spantitle">修改密码</span>
          </template>
          <div>
            <el-button type="text" @click="dialogFormVisible = true">点我修改密码</el-button>
            <el-dialog title="修改密码" :visible.sync="dialogFormVisible">
              <el-form :model="form" :rules="rules" ref="changeFormRef">
                <el-form-item label="账号" label-width="120px">
                  {{userInfo.username}}
                </el-form-item>
                <el-form-item label="原密码" label-width="120px" prop="oldpwd">
                  <el-input v-model="form.oldpwd" auto-complete="off" style="width:500px"></el-input>
                </el-form-item>
                <el-form-item label="新密码" label-width="120px" prop="newpwd">
                  <el-input v-model="form.newpwd" auto-complete="off" style="width:500px" minlength="5" maxlength="30"></el-input>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="cancelChange">取 消</el-button>
                <el-button type="primary" @click="changePassword">确 定</el-button>
              </div>
            </el-dialog>
          </div>
        </el-collapse-item>
      </el-collapse>
    </el-card>
  </div>
</template>

<script>
export default {
    name:"MyInfo",
    data() {
      return {
        activeNames:['1'],
        userInfo:{},
        major:{},
        college:{},
        dialogFormVisible: false,
        form: {
          oldpwd: '',
          newpwd: '',
        },
        // 表单规则
        rules: {
            oldpwd: [
                { required: true, message: '您还未输入原密码', trigger: 'blur' },
                { min: 5, max: 30, message: '长度在 5 到 30 个字符', trigger: 'blur' }
            ],
            newpwd: [
                { required: true, message: '您还未输入新密码', trigger: 'blur' },
                { min: 5, max: 30, message: '长度在 5 到 30 个字符', trigger: 'blur' }
            ],
        }
      }
    },
    methods:{
      cancelChange(){
        this.dialogFormVisible = false ;
        // this.$message({message: '已退出修改密码'});
        this.$notify({
          title: '取消修改',
          message: "您已退出修改密码！",
          type: 'info',
          offset:100
        });
      },
      changePassword(){
        this.$refs.changeFormRef.validate(valid=>{
            // 无效数据直接发回
            if(!valid) return;
            let data = {
              oldpwd:this.form.oldpwd,
              newpwd:this.form.newpwd,
            }
            this.$api.userInfo.changePassword(sessionStorage.getItem("username"),data)
            .then((res)=>{
              // this.$message({
              //   message:res.data["msg"],
              //   type:"success",
              // });
              this.$notify({
                title: '修改成功',
                message: res.data["msg"],
                type: 'success',
                offset:100
              });
              this.dialogFormVisible = false;
            })
            .catch((err)=>{
              this.$message({
                  message:err.response.data["msg"],
                  type:"error",
                });
              // this.$notify({
              //   title: '修改失败',
              //   message: err.response.data["msg"],
              //   type: 'error',
              //   offset:100
              // });
            });
        });
      }
    },
    mounted() {
      this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
      .then(res=>{
        this.userInfo = res.data["data"]["user"];
        this.major = res.data["data"]["major"];
        let idtf = sessionStorage.getItem("identity");
        if(idtf==1)
          this.userInfo.identity = "学生";
        else if(idtf==2)
          this.userInfo.identity = "教师";
        else if(idtf==3)
          this.userInfo.identity = "管理员";
        // 获取学院信息
        this.$api.userInfo.getCollege(this.major.pcode)
        .then(res=>{
          this.college = res.data["data"]["college"];
        })
        .catch(()=>{
          this.college = this.major;
        });
      })
    }
}
</script>

<style scoped>
  .info div{
    height: 30px;
  }
  .myinfo_all div{
    font-size: 15px;
    color: #606266;
  }

</style>