<template>
  <div class="UserInfo_all">
    <div class="img_1">
        <img src="../assets/img/TONGJI1030.png" alt="头像" width="55" height="55">
    </div>
    <div>
    </div>
    <div class="info_1">
        <span class="Hi">Hi ~ {{userInfo.fullname}}</span>
        <div class="tag">
            <el-tag size="medium">{{userInfo.identity}}</el-tag>
            &nbsp;
            <el-tag size="medium">账号：{{userInfo.username}}</el-tag>
        </div>
    </div>
    <div>
        <el-dropdown style="width:100px">
        <span class="el-dropdown-link" style="color:#fff;font-size:18px;">
            我的信息<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown" style="margin:30">
            <div style="padding:10px 20px;">我的信息</div> 
            <el-dropdown-item>姓名：{{userInfo.fullname}}</el-dropdown-item>
            <el-dropdown-item>账号：{{userInfo.username}}</el-dropdown-item>
            <el-dropdown-item>身份：{{userInfo.identity}}</el-dropdown-item>
            <el-dropdown-item>学院：{{college.majorcode}} - {{college.title}}</el-dropdown-item>
            <el-dropdown-item>专业：{{major.majorcode}} - {{major.title}}</el-dropdown-item>
            <el-dropdown-item>当前学期：{{userInfo.term}} - {{userInfo.stage}}</el-dropdown-item>
            <el-button type="info" plain @click="change" icon="el-icon-edit" style="margin:10px 30px;">修改</el-button>
            <el-button type="info" plain @click="logOut" icon="el-icon-remove" style="margin:10px 30px;">退出</el-button>
        </el-dropdown-menu>
        </el-dropdown>
    </div>
  </div>
</template>

<script>
export default {
    name:"UserInfo",
    data() {
      return {
        userInfo:{},
        major:{},
        college:{},
      }
    },
    methods: {
        logOut(){
            sessionStorage.removeItem('aams-token');
            sessionStorage.clear();
            this.$router.push('/login');
            this.$message({
                message: '成功退出，再见！',
                type: 'success'
            });
        },
        change(){
            if(this.$router.history.current.path!=='/student/myInfo'){
                this.$router.push('/student/myInfo');
            }
            else{
                this.$message({message:"已经到达修改页面！"});
            }
        }
    },
    mounted() {
        this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
        .then(res=>{
            this.userInfo = res.data["data"]["user"];
            this.major = res.data["data"]["major"];
            let idtf = sessionStorage.getItem("identity");
            if(idtf==1)
            this.userInfo.identity = "学生";
            else if(idtf==2)
            this.userInfo.identity = "教师";
            else if(idtf==3)
            this.userInfo.identity = "管理员";
            // 获取学院信息
            this.$api.userInfo.getCollege(this.major.pcode)
            .then(res=>{
            this.college = res.data["data"]["college"];
            })
            .catch(()=>{
            this.college = this.major;
            });
        })
    }
}
</script>

<style scoped>
    .UserInfo_all{
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-left: 30px;
        width: 400px;
        height: 100%;
    }
    .img_1{
        width: 12%;
    }
    .info_1{
        display: flex;
        flex-direction: column;
        width: 66%;
        margin-left: 20px;
        margin-right: 20px;
    }
    .Hi{
        font-size: 20px;
        margin-bottom: 5px;
    }
    .tag{
        display: flex;
        flex-direction: row;
        justify-content: start;
    }
    .btn_1{
        background-color: #fff;
    }
</style>