<template>
  <div>
    <div class="nav_title">全校课程</div>
    <el-form :inline="true" :model="askFrom" class="demo-form-inline">
      <el-form-item label="学期代码">
        <el-input v-model="askFrom.term" placeholder="学期代码"></el-input>
      </el-form-item>
      <el-form-item label="校区">
        <el-select v-model="askFrom.campus" placeholder="校区">
          <el-option label="所有校区" value="9"></el-option>
          <el-option label="四平校区" value="1"></el-option>
          <el-option label="嘉定校区" value="2"></el-option>
          <el-option label="其他校区" value="0"></el-option>
          <el-option label="线上课堂" value="3"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchCourse">查询</el-button>
      </el-form-item>
    </el-form>
    <el-card class="box-card">
      <el-table stripe :data="showCourse">
        <el-table-column prop="coursecode" label="课程代码" align="center" width="150"></el-table-column>
        <el-table-column prop="title" label="课程名称" align="center" width="200"></el-table-column>
        <el-table-column prop="teacher.major.title" label="开课学院" align="center" width="120"></el-table-column>
        <el-table-column prop="teacher.fullname" label="授课教师" align="center" width="120"></el-table-column>
        <el-table-column prop="credits" label="学分" align="center" width="80"></el-table-column>
        <el-table-column label="校区" align="center" width="120">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.campus==1" type="success">四平校区</el-tag>
            <el-tag v-else-if="scope.row.campus==2">嘉定校区</el-tag>
            <el-tag v-else-if="scope.row.campus==0" type="warning">其他校区</el-tag>
            <el-tag v-else-if="scope.row.campus==3" type="info">线上课堂</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="info" label="教学信息" align="center"></el-table-column>
        <el-table-column label="类型" align="center" width="80">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.coursetype==1">必修</el-tag>
            <el-tag v-else-if="scope.row.coursetype==2" type="success">选修</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="capacity" label="容量" align="center" width="80"></el-table-column>
        <el-table-column prop="selectednumber" label="已选" align="center" width="80"></el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[5,10,20]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="allCourse.length"
        style="margin:20px 0;float:right;">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
    name:"SchoolCourse",
    data() {
      return {
        allCourse:[],
        askFrom:{
          term:1,
          campus:"9",
        },
        currentPage:1,
        pagesize:10,
        showCourse:[],
      }
    },
    methods: {
      changeShowCourse(){
        this.showCourse = this.allCourse.slice((this.currentPage-1)*this.pagesize,this.currentPage*this.pagesize);
      },
      handleSizeChange(val){
        this.pagesize = val;
        this.changeShowCourse();
      },
      handleCurrentChange(val){
        this.currentPage = val;
        this.changeShowCourse();
      },
      searchCourse(){
        this.$api.course.getAllCourse(this.askFrom.term,this.askFrom.campus)
        .then(res=>{
          this.allCourse = res.data["data"]["courses"];
          this.currentPage = 1;
          // this.$message({
          //   message:"查询成功！",
          //   type:"success",
          // });
          this.$notify({
            title: '查询成功',
            message: '查询成功，信息如表所示！',
            type: 'success',
            offset:100
          });
          this.changeShowCourse();
        })
        .catch(err=>{
          console.log(err)
          // this.$message({
          //   message:"查询失败！",
          //   type:"error",
          // })
          this.$notify({
            title: '查询失败',
            message: err.data.response["msg"],
            type: 'error',
            offset:100
          });
        })
      }
    },
}
</script>

<style scoped>

</style>