<template>
  <div>
    <el-card>
    <div class="nav_title">当前学期阶段</div>
      <el-steps :active="userInfo.stage-1" finish-status="success" simple style="margin-top: 20px">
        <el-step title="新学期初" ></el-step>
        <el-step title="开放选课"></el-step>
        <el-step title="稳定学习"></el-step>
        <el-step title="期末考试"></el-step>
        <el-step title="学期结束"></el-step>
      </el-steps>
    </el-card>
    <br> <br>
    <el-card>
    <div class="nav_title" style="text-align:center;">我的成绩单</div>
      <el-table
      :data="grades"
      border
      :default-sort="{prop:'term',order:'ascending'}">
      <el-table-column prop="term" label="学期编号" align="center" sortable  width="120"></el-table-column>
      <el-table-column prop="cid.coursecode" label="课程代码" align="center" width="140"></el-table-column>
      <el-table-column prop="cid.title" label="课程名称" align="center" show-overflow-tooltip></el-table-column>
      <el-table-column prop="cid.credits" label="学分" align="center" width="100"></el-table-column>
      <el-table-column label="课程类型" align="center" width="80">
        <template slot-scope="scope">
          <div v-if="scope.row.cid.coursetype==1">必</div>
          <div v-else-if="scope.row.cid.coursetype==2">选</div>
          <div v-else>待定</div>
        </template>
      </el-table-column>
      <el-table-column prop="score1" label="考勤" align="center" width="80"></el-table-column>
      <el-table-column prop="score2" label="过程性1" align="center" width="80"></el-table-column>
      <el-table-column prop="score3" label="过程性2" align="center" width="80"></el-table-column>
      <el-table-column prop="score4" label="期末" align="center" width="80"></el-table-column>
      <el-table-column prop="" label="成绩" align="center" width="100">
        <template slot-scope="scope">
          <div v-if="scope.row.gradepoint==0">不及格</div>
          <div v-else-if="scope.row.gradepoint==2">及格</div>
          <div v-else-if="scope.row.gradepoint==3">中</div>
          <div v-else-if="scope.row.gradepoint==4">良</div>
          <div v-else-if="scope.row.gradepoint==5">优</div>
          <div v-else> </div>
        </template>
      </el-table-column>
      <el-table-column prop="gradepoint" label="绩点" align="center" width="100"></el-table-column>
      <el-table-column prop="updated_at" label="更新时间" align="center" width="100" show-overflow-tooltip></el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
    name:"CheckGrades",
    data() {
      return {
        userInfo:{},
        grades:[],
      }
    },
    methods:{
      getMyGrades(){
        this.$api.grades.getGrades(this.userInfo.username)
        .then(res=>{
          this.grades = res.data["data"]["grades"];
        })
      }
    },
    created(){
      this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
      .then(res=>{
        this.userInfo = res.data["data"]["user"];
        this.getMyGrades();
      });
    }
}
</script>

<style scoped>
  .spantitle{
    font-size:16px;
    color:#303133;
    font-weight: bold;
  }
</style>