<template>
  <div>
    <div class="nav_title">我的授课</div>
    <el-card>
      <el-table
      :data="courses"
      :default-sort="{prop:'term',order:'descending'}">
        <el-table-column prop="term" label="学期编码" align="center" width="120" sortable></el-table-column>
        <el-table-column prop="coursecode" label="课程代码" align="center" width="140"></el-table-column>
        <el-table-column prop="title" label="课程名称" align="center" show-overflow-tooltip></el-table-column>
        <el-table-column prop="credits" label="学分" align="center" width="120"></el-table-column>
        <el-table-column label="课程类型" align="center" width="80">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.coursetype==1">必修</el-tag>
            <el-tag v-else-if="scope.row.coursetype==2" type="success">选修</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="校区" align="center" width="150">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.campus==1" type="success">四平校区</el-tag>
            <el-tag v-else-if="scope.row.campus==2">嘉定校区</el-tag>
            <el-tag v-else-if="scope.row.campus==3" type="warning">其他校区</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="info" label="教学信息" align="center"></el-table-column>
        <el-table-column prop="capacity" label="容量" align="center" width="100"></el-table-column>
        <el-table-column prop="selectednumber" label="已选" align="center" width="100"></el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
    name:"MyTeach",
    data() {
      return {
        userInfo:{},
        courses:[],
      }
    },
    methods:{
      getMyTeach(){
        this.$api.course.getTeach(this.userInfo.username)
        .then(res=>{
          this.courses = res.data["data"]['courses'];
        })
      }
    },
    created(){
      this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
      .then(res=>{
        this.userInfo = res.data["data"]["user"];
        this.getMyTeach();
      })
    }
}
</script>

