<template>
  <div class="LoginPage_all">
    <div class="login_container">
        <div class="logintitle">同济大学教学信息管理系统</div>
        <el-form :model="loginForm" :rules="rules" ref="loginFormRef" label-width="80px" class="loginform_all">
            <el-form-item label="账号" prop="account">
                <el-input placeholder="请输入账号" v-model="loginForm.account" prefix-icon="el-icon-star-off"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
                <el-input placeholder="请输入密码" v-model="loginForm.password" :type="isShow?'text':'password'" prefix-icon="el-icon-view"></el-input>
            </el-form-item>
            <el-switch v-model="isShow" active-text="显示密码" class="switch"></el-switch>
            <el-form-item class="btns">
                <el-button type="primary" @click="login" class="btn" icon="el-icon-circle-check">登陆</el-button>
                <!-- <el-button type="info" class="btn" @click="signUp" icon="el-icon-edit">注册</el-button> -->
            </el-form-item>
        </el-form>
    </div>
  </div>
</template>

<script>
export default {
    name:"LoginPage",
    data() {
        return {
            // 是否显示密码（已经直接关联在input的type上了）
            isShow:false,
            // 表单数据
            loginForm:{
                account:"",
                password:"",
            },
            // 表单规则
            rules: {
                account: [
                    { required: true, message: '您还未输入账号', trigger: 'blur' },
                    { min: 4, max: 7, message: '长度在 4 到 7 个字符', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '您还未输入密码', trigger: 'blur' },
                    { min: 5, max: 30, message: '长度在 5 到 30 个字符', trigger: 'blur' }
                ],
            }
        }
    },
    methods: {
        login(){
            // 表单提交，使用refs属性定位到表单，先执行表单是否有效数据
            this.$refs.loginFormRef.validate(valid=>{
                // 无效数据直接发回
                if(!valid) return;
                console.log(this.$api)
                this.$api.userInfo.login({
                    username:this.loginForm.account,
                    password:this.loginForm.password
                })
                .then((res) => {
                    // console.log('请求成功',res.data)
                    this.$message({
                    message: '登录成功，欢迎您！',
                    type: 'success'
                    });
                    // 存储sessionStorage
                    // console.log(res.data.data["aams-token"])
                    sessionStorage.setItem("aams-token",res.data["data"]["aams-token"]);
                    sessionStorage.setItem("username",this.loginForm.account);
                    sessionStorage.setItem("identity",res.data['data']['identity']);
                    // 编程式导航跳转后台主页
                    this.$router.push('/home');
                })
                .catch(()=>{
                    // console.log('请求失败',error.message);
                    this.$message.error('抱歉，账号或密码有误！');
                })
                
            })
            
        },
        // signUp(){
        //     this.$message.error('抱歉，功能还未开发...');
        // }
    },
}
</script>

<style scoped>
    .LoginPage_all{
        height: 100%;
        background-image: url('../assets/img/TONGJI1028.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .login_container{
        background-color: #F2F6FC;
        width: 500px;
        height: 380px;
        border-radius: 10px;
        flex-direction: column;
        box-shadow: 0 0 8px #ddd;
        position: absolute;
    }
    .logintitle{
        padding-top: 40px;
        padding-bottom: 10px;
        font-size: 25px;
        text-align: center;
    }
    .loginform_all{
        padding-top: 30px;
        padding-left: 40px;
        padding-right: 80px;
    }
    .switch{
        position: absolute;
        right: 80px;
    }
    .btns{
        margin-top: 70px;
        display: flex;
        flex-direction: row;
        justify-content: center;
    }
    .btn{
        /* width: 100px; */
        width: 300px;
        margin-right: 25px;
    }
</style>