<template>
  <div>
    <el-card>
    <div class="nav_title">通知详情</div>
        <div v-for="(notice,index) in notices" :key="index" class="text item">
        <el-row>
            <el-col :span="8"><div class="release"><el-button type="text" style="color:black;">{{ notice.release }}</el-button></div></el-col>
            <el-col :span="16">
            <el-button type="text" @click="showNotice(index)">{{ notice.title }}</el-button>
                <el-dialog
                :title="showMsg.title"
                :visible.sync="dialogVisible"
                width="60%">
                <span>{{showMsg.content}}</span>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false;">取 消</el-button>
                    <el-button type="primary" @click="dialogVisible = false;">确 定</el-button>
                </span>
                </el-dialog>
            </el-col>
        </el-row>
        </div>
    </el-card>
  </div>
</template>

<script>
export default {
    name:"MoreNotice",
    data() {
        return {
            notices:{},
            dialogVisible:false,
            showMsg:{},
        }
    },
    methods:{
      showNotice(key){
        this.dialogVisible = true;
        this.showMsg = this.notices[key];
      },
    },
    mounted(){
      this.$api.notice.getNotices()
      .then(res=>{
        this.notices = res.data["data"]["notices"],
        this.showMsg = this.notices[0];
      })
      .catch(err=>{
        console.log(err)
      });
    }
}
</script>

