import http from '../utils/http'
import qs from 'qs'

// 获取学生的成绩单
export function getGrades(username){
    return http({
        method:"GET",
        url:`getGrades/?username=${username}`,
    })
}

// 获取某一课程的学生的成绩记录
export function tryGetGrade(cid,sid){
    return http({
        method:"GET",
        url:`tryGetGrade/?cid=${cid}&sid=${sid}`,
    })
}

// 录入一个成绩
export function writeGrade(data={}){
    return http({
        method:"POST",
        url:"writeGrade/",
        data:qs.stringify(data),
    })
}