//该文件专门用于创建整个应用的路由器
import VueRouter from 'vue-router'
//引入所需组件
// 页面（登陆、主页）
import LoginPage from '../pages/LoginPage'
import HomePage from '../pages/HomePage'
// 学生操作
import SelectCourse from '../operations/SelectCourse'
import SelectCourseInfo from '../operations/SelectCourseInfo'
import FirstWelcome from '../operations/FirstWelcome'
import CheckGrades from '../operations/CheckGrades'
import MySchedule from '../operations/MySchedule'
import MyInfo from '../operations/MyInfo'
import SchoolCourse from '../operations/SchoolCourse'
import ApplyFor from '../operations/ApplyFor'
import ProvideFeedback from '../operations/ProvideFeedback'
// 老师操作
import MyTeach from '../operations/MyTeach'
import WriteGrades from '../operations/WriteGrades'
// 通知详情
import MoreNotice from '../operations/MoreNotice'
import AboutSystem from '../operations/AboutSystem'
//下载页面
import DownloadPage from '../operations/DownloadPage'

//创建一个路由器
const router =  new VueRouter({
    routes:[
        {
            path:"/",
            redirect:"/login",
        },
        {
            name:"login",
            path:"/login",
            component:LoginPage,
        },
        {
            name:"home",
            path:"/home",
            component:HomePage,
            redirect:"/home/welcome",
            children:[
                {
                    name:"welcome",
                    path:"welcome",
                    component:FirstWelcome
                }
            ]
        },
        {
            name:"student",
            path:"/student",
            component:HomePage,
            children:[
                {
                    name:"schoolCourse",
                    path:"schoolCourse",
                    component:SchoolCourse,
                },
                {
                    name:"selectCourse",
                    path:"selectCourse",
                    component:SelectCourse,
                },
                {
                    name:"downloadPage",
                    path:"downloadPage",
                    component:DownloadPage,
                },
                {
                    name:"selectCourseInfo",
                    path:"selectCourseInfo",
                    component:SelectCourseInfo,
                },
                {
                    name:"checkGrades",
                    path:"checkGrades",
                    component:CheckGrades,
                },
                {
                    name:"mySchedule",
                    path:"mySchedule",
                    component:MySchedule,
                },
                {
                    name:"myInfo",
                    path:"myInfo",
                    component:MyInfo,
                },
                {
                    name:"applyFor",
                    path:"applyFor",
                    component:ApplyFor
                },
                {
                    name:"provideFeedback",
                    path:"provideFeedback",
                    component:ProvideFeedback
                },
                {
                    name:"moreNotice",
                    path:"moreNotice",
                    component:MoreNotice
                },
                {
                    name:"aboutSystem",
                    path:"aboutSystem",
                    component:AboutSystem
                }
            ]
        },
        {
            name:"teacher",
            path:"/teacher",
            component:HomePage,
            children:[
                {
                    name:"MyTeach",
                    path:"myTeach",
                    component:MyTeach
                },
                {
                    name:"WriteGrades",
                    path:"writeGrades",
                    component:WriteGrades
                }
            ]
        },
    ]
})

router.beforeEach((to,from,next) => {
    // 登录页面直接放行
    if(to.path === "/login") return next();
    // 其他情况查看tooken，正确则放行，不正确不放行
    const tokenStr = sessionStorage.getItem('aams-token');
    if(!tokenStr)
        return next('/login');
    else
        return next();

});

export default router