<template>
  <div class="FirstWelcome_all">
    <!-- <div class="welcome">欢迎您 ~ </div> -->
    <el-carousel :interval="4000" type="card" height="300">
      <el-carousel-item v-for="notice in notices" :key="notice.number">
        <div class="content">
          <h3>{{notice.title}} {{notice.release}}</h3>
          <p>{{notice.content}}</p>
        </div>
      </el-carousel-item>
    </el-carousel>
    <div style="width:100%;display:flex;">
      <div style="width:50%;margin-right:10px;">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <h3>本学期进度</h3>
          </div>
          <div>
            <el-row>
              <el-col :span="8">
                <el-progress type="circle" :percentage="userInfo.stage*20"/>
              </el-col>
              <el-col :span="16">
                <h4>{{userInfo.fullname}} {{userInfo.username}}</h4>
                <h5>当前学期：{{userInfo.term}}</h5>
                <h5>学期阶段：{{userInfo.stage}} / 5</h5>
                <h5>学院：{{college.majorcode}} - {{college.title}}</h5>
                <h5>专业：{{major.majorcode}} - {{major.title}}</h5>
              </el-col>
            </el-row>
          </div>
        </el-card>
      </div>
      <div style="width:50%;margin-left:10px;">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <h3>通知公告</h3>
          </div>
          <div v-for="(notice,index) in notices.slice(0,4)" :key="index" class="text item">
            <el-row>
              <el-col :span="8"><div class="release"><el-button type="text" style="color:black;">{{ notice.release }}</el-button></div></el-col>
              <el-col :span="16">
                <el-button type="text" @click="showNotice(index)">{{ notice.title }}</el-button>
                  <el-dialog
                    :title="showMsg.title"
                    :visible.sync="dialogVisible"
                    width="60%">
                    <span>{{showMsg.content}}</span>
                    <span slot="footer" class="dialog-footer">
                      <el-button @click="dialogVisible = false">取 消</el-button>
                      <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
                    </span>
                  </el-dialog>
              </el-col>
            </el-row>
          </div>
        </el-card>
      </div>
    </div>
    
  </div>
</template>

<script>
export default {
    name:"FirstWelcome",
    data() {
      return {
        notices:{},
        dialogVisible: false,
        showMsg:{},
        userInfo:{},
        major:{},
        college:{},

      }
    },
    methods:{
      showNotice(key){
        this.dialogVisible = true;
        this.showMsg = this.notices[key];
      },
    },
    mounted(){
      this.$api.notice.getNotices()
      .then(res=>{
        this.notices = res.data["data"]["notices"];
        this.showMsg = this.notices[0];
      })
      .catch(err=>{
        console.log(err)
      });
      this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
        .then(res=>{
            this.userInfo = res.data["data"]["user"];
            this.major = res.data["data"]["major"];
            let idtf = sessionStorage.getItem("identity");
            if(idtf==1)
              this.userInfo.identity = "学生";
            else if(idtf==2)
              this.userInfo.identity = "教师";
            else if(idtf==3)
              this.userInfo.identity = "管理员";
            this.$api.userInfo.getCollege(this.major.pcode)
            .then(res=>{
              this.college = res.data["data"]["college"];
            })
            .catch(()=>{
              this.college = this.major;
            })
        })
    }
}
</script>

<style scoped>
  .FirstWelcome_all{
      width: 100%;
      height: 100%;
  }
  .welcome{
      margin-left: 30px;
      font-size: 30px;
  }
  .showimg{
    height: 100%;
    width: 100%;
    object-fit:cover;
  }
  .el-carousel__item{
    background-color: #E4E7ED;
  }
  .content {
    color: #303133;
    font-size: 14px;
    margin: 20px 30px;
  }
  .text {
    font-size: 16px;
  }
  .item {
    margin-bottom: 18px;
  }
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }
  .box-card {
    width: 100%;
  }
</style>