<template>
  <div>
    <!-- 流程示意图 -->
    <el-card>
    <div class="nav_title" style="display:flex; justify-content:center;">
      <el-button @click="renext">上一步</el-button>
      <div style="margin:5px 20px;">录入成绩流程</div>
      <el-button @click="next">下一步</el-button>
    </div>
    <br> <br>
    <div>
      <el-steps :active="active" simple finish-status="success">
        <el-step title="选择课程" icon="el-icon-search"></el-step>
        <el-step title="选择学生" icon="el-icon-upload"></el-step>
        <el-step title="录入成绩" icon="el-icon-edit"></el-step>
      </el-steps>
      <el-progress :percentage="active/3*100" status="success"></el-progress>
    </div>
    </el-card>
    <br>
    <!-- 具体操作 -->
    <el-card>
      <template v-if="userInfo.stage!=5">
        <div style="text-align:center;">
          <div class="nav_title" style="font-size:30px; margin:30px 0;">未开放！</div>
          <div class="spantitle">本学期还未到学期结束阶段，成绩录入权限暂未开放！<br> <br> （如有特殊情况，请联系管理员） </div>
          <br> <br>
        </div>
      </template>
      <!-- 选择课程 -->
      <template v-if="userInfo.stage==5 && active==0">
        <div>
          <el-table
          :data="courses"
          :default-sort="{prop:'term',order:'descending'}">
            <el-table-column prop="term" label="学期编码" align="center" width="120" sortable></el-table-column>
            <el-table-column prop="coursecode" label="课程代码" align="center"></el-table-column>
            <el-table-column prop="title" label="课程名称" align="center"></el-table-column>
            <el-table-column label="操作" align="center">
              <template slot-scope="scope">
                <div style="display:none;">{{scope.row.title}}</div>
                <el-button type="primary" @click="clickCourse(scope.row.coursecode)">选中</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </template>
      <!-- 选择学生 -->
      <template v-if="userInfo.stage==5 && active==1">
        <div>
          <div style="margin:20px 40px 0;" class="spantitle">课程代码：{{activeCourse}}</div>
        </div>
        <br>
        <div>
          <el-table
          :data="students"
          :default-sort="{prop:'term',order:'descending'}">
            <el-table-column prop="username" label="学号" align="center" width="120" sortable></el-table-column>
            <el-table-column prop="fullname" label="姓名" align="center"></el-table-column>
            <el-table-column prop="major.title" label="专业" align="center"></el-table-column>
            <el-table-column label="操作" align="center">
              <template slot-scope="scope">
                <div style="display:none;">{{scope.row.username}}</div>
                <el-button type="primary" @click="clickStudent(scope.row.username)">选中</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </template>
      <!-- 录入成绩 -->
      <template v-if="userInfo.stage==5 && active==2">
      <div style="text-align:center;">
        <div>
          <el-row style="margin:20px 0;" class="spantitle">
            <el-col :span="4"><div>课程代码：{{activeCourse}}</div></el-col>
            <el-col :span="4"><div>学生学号：{{activeStudent}}</div></el-col>
          </el-row>
        </div>
        <br>
        <div>
          <el-row style="margin:20px 0;" class="spantitle">
            <el-col :span="4" :offset="4"><div>考勤</div></el-col>
            <el-col :span="4"><div>过程性1</div></el-col>
            <el-col :span="4"><div>过程性2</div></el-col>
            <el-col :span="4"><div>期末</div></el-col>
          </el-row>
        </div>
        <div>
          <el-form :inline="true" :model="percentage" class="demo-form-inline">
            <el-form-item label="占比" style="font-weight:bold;"></el-form-item>
            <el-form-item>
              <el-input v-model="percentage.score1" placeholder="考勤(1 - 100)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="percentage.score2" placeholder="过程性1(1 - 100)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="percentage.score3" placeholder="过程性2(1 - 100)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="percentage.score4" placeholder="期末(1 - 100)"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <div>
          <el-form :inline="true" :model="score" class="demo-form-inline">
            <el-form-item label="成绩" style="font-weight:bold;">
            </el-form-item>
            <el-form-item>
              <el-input v-model="score.score1" placeholder="考勤(1 - 100)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="score.score2" placeholder="过程性1(1 - 100)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="score.score3" placeholder="过程性2(1 - 100)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-input v-model="score.score4" placeholder="期末(1 - 100)"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <div class="spantitle" style="margin:20px 0 20px 500px; text-align:start; ">综合分数：<span style="margin-left:10px;">{{gradepoint1}}</span></div>
        <div class="spantitle" style="margin:20px 0 20px 500px; text-align:start; ">课程绩点：<span style="margin-left:10px;">{{gradepoint2}}</span></div>
        <el-rate
          v-model="gradepoint2"
          disabled
          show-score
          text-color="#ff9900"
          score-template="{value}">
        </el-rate>
        <br>
        <el-button type="primary" @click="submitGrade">录入成绩</el-button>
        <br> <br> <br>
        <div style="text-align:start; margin:50px;">
          <span class="spantitle">成绩录入说明：</span>
          <p>（1）录入成绩前，请先检查课程课号、学生学号是否正确！</p>
          <p>（2）录入成绩时，分为考勤、过程性测试1、过程性测试2、期末考核四部分，每部分有各自的占比和成绩。</p>
          <p>（3）占比：每个部分占总成绩的百分比，四部分加和应为100，不必输入%，计算时会自动计算为百分比。</p>
          <p>（4）成绩：每部分下学生获得的成绩，每部分成绩都是0-100分。</p>
        </div>
        <br>
      </div>
      </template>
      <!-- 完成 -->
      <template v-if="userInfo.stage==5 && active==3">
        <div style="text-align:center;">
          <div class="nav_title" style="font-size:30px; margin:30px 0;">录入完成！</div>
          <div class="spantitle">您已完成一次成绩录入，<br> 点击继续，录入其他成绩！</div>
          <br> <br>
        <el-button type="primary" @click="next">继续</el-button>
        </div>
      </template>
    </el-card>
  </div>
</template>

<script>
export default {
    name:"WriteGrades",
    data() {
      return {
        // 预加载数据
        userInfo:{},
        courses:[],
        // 步骤
        active:0,
        // 选择课程
        activeCourse:"",
        students:[],
        activeStudent:"",
        score:{"score1":0,"score2":0,"score3":0,"score4":0},
        percentage:{"score1":10,"score2":20,"score3":30,"score4":40}
      }
    },
    computed:{
      gradepoint1(){
        let allscore = 0.01*(this.score.score1*this.percentage.score1+this.score.score2*this.percentage.score2+this.score.score3*this.percentage.score3+this.score.score4*this.percentage.score4);
        return allscore;
      },
      gradepoint2(){
        let allscore = 0.01*(this.score.score1*this.percentage.score1+this.score.score2*this.percentage.score2+this.score.score3*this.percentage.score3+this.score.score4*this.percentage.score4);
        if(allscore>=90){
          return 5
        }
        else if(allscore>=80){
          return 4
        }
        else if(allscore>=70){
          return 3
        }
        else if(allscore>=60){
          return 2
        }
        else{
          return 0
        }
      }
    },
    methods:{
      // 上一步、下一步
      next() {
        if (this.active++ > 2) this.active = 0;
      },
      renext(){
        if (this.active-- < 1 ) this.active = 3;
      },

      // 1.选中课程
      clickCourse(code){
        this.activeCourse=code;
        // 获取课程下的学生
        this.$api.course.getCourseStudents(this.activeCourse)
        .then(res=>{
          this.students = res.data["data"]["students"];
        })
        this.next();
      },

      // 2.选中学生
      clickStudent(username){
        this.activeStudent = username;
        // 尝试获取是否已有成绩
        this.$api.grades.tryGetGrade(this.activeCourse,this.activeStudent)
        .then(res=>{
          if(res.data["code"]==200){
            this.score.score1 = res.data["data"]["grade"]['score1'];
            this.score.score2 = res.data["data"]["grade"]['score2'];
            this.score.score3 = res.data["data"]["grade"]['score3'];
            this.score.score4 = res.data["data"]["grade"]['score4'];
          }
        })
        this.next();
      },

      // 3.录入成绩
      submitGrade(){
        let data = {
          "term":this.userInfo.term,
          "cid":this.activeCourse,
          "sid":this.activeStudent,
          "score1":this.score.score1,
          "score2":this.score.score2,
          "score3":this.score.score3,
          "score4":this.score.score4,
          "gradepoint":this.gradepoint2,
        }
        this.$api.grades.writeGrade(data)
        .then(()=>{
          this.$notify({
            title: '录入成功',
            message: '您已成功更新录入该学生成绩！',
            type: 'success',
            offset:150
          });
          this.next();
        })
        .catch(()=>{
          this.$notify({
            title: '录入失败',
            message: '网络出错或录入信息有误！',
            type: 'error',
            offset:150
          });
        })
      },

      // 获取基本信息
      getMyTeach(){
        this.$api.course.getTeach(this.userInfo.username)
        .then(res=>{
          this.courses = res.data["data"]['courses'];
        })
      }
    },
    created(){
      this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
      .then(res=>{
        this.userInfo = res.data["data"]["user"];
        this.getMyTeach();
      })
    }
}
</script>

<style scoped>

</style>