<template>
  <div>
    <!-- 学生课表 -->
    <div class="nav_title">学生课表 <el-button type="text" @click="reloadSchedule">刷新</el-button></div> 
    <el-card :key="scheduleKey">
      <ScheduleTable :schedule="schedule"/>
    </el-card>
    <!-- 已选课程列表 -->
    <br> <br>
    <div class="nav_title">已选课程列表</div>
    <el-card>
      <el-table stripe :data="scheduleCourse">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="coursecode" label="课程代码" align="center" width="100"></el-table-column>
        <el-table-column prop="title" label="课程名称" align="center" width="200"></el-table-column>
        <el-table-column prop="teacher.major.title" label="开课学院" align="center" width="150"></el-table-column>
        <el-table-column prop="teacher.fullname" label="授课教师" align="center" width="100"></el-table-column>
        <el-table-column prop="credits" label="学分" align="center" width="80"></el-table-column>
        <el-table-column label="校区" align="center" width="120">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.campus==1" type="success">四平校区</el-tag>
            <el-tag v-else-if="scope.row.campus==2">嘉定校区</el-tag>
            <el-tag v-else-if="scope.row.campus==0" type="warning">其他校区</el-tag>
            <el-tag v-else-if="scope.row.campus==3" type="info">线上课堂</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="info" label="教学信息" align="center"></el-table-column>
        <el-table-column label="类型" align="center" width="80">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.coursetype==1">必修</el-tag>
            <el-tag v-else-if="scope.row.coursetype==2" type="success">选修</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="capacity" label="容量" align="center" width="80"></el-table-column>
        <el-table-column prop="selectednumber" label="已选" align="center" width="80"></el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import ScheduleTable from './ScheduleTable';
export default {
    name:"MySchedule",
    components:{
      ScheduleTable,
    },
    data() {
      return {
        scheduleCourse:[],
        schedule:{},
        scheduleKey:0,
      }
    },
    methods:{
      // 刷新课表
      reloadSchedule(){
        this.loadSchedule();
        this.scheduleKey++;
        const h = this.$createElement;
        this.$notify({
          title: '刷新成功',
          message: h('i', { style: 'color: teal'}, '刷新课表成功！'),
          position: 'top-left',
          offset:150
        });
      },
      // 加载课表
      loadSchedule(){
        this.$api.userInfo.getUserInfo(sessionStorage.getItem("username"))
        .then(res=>{
          this.scheduleCourse = res.data['data']['user']['courses'];
          this.schedule = this.$api.course.readSchedule(this.scheduleCourse);
        })
        .catch(err=>{
          this.$message({
            message:"获取课表出错！"+err.response.data["msg"],
            type:"error"
          })
        });
      },
    },
    created(){
      this.loadSchedule();
    },
    mounted(){

    },
    activated(){
      this.loadSchedule();
      this.scheduleKey++;
    }
    
}
</script>

<style scoped>

</style>